import {
	mapState,
	mapMutations
} from 'vuex';
import mInput from '../../components/m-input.vue'
import sunTab from '@/components/sun-tab/sun-tab.vue';
import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"

const BASE_URL = 'http://www.luominus.com/';
import uniRequest from 'uni-request';
export default {
	computed: mapState(['forcedLogin', 'hasLogin', 'userName','avatarUrl']),
	components: {
		sunTab,mInput,uniNavBar
	},
	data() {
		return {
			swiperIndex:0,
			index: 0,
			account: '',
			tabSwiperList: ['支付宝','微信',],
			treatmentStatu: { checked: false },
			menus : {
						"wx": {
				            "img": "http://www.luominus.com/",
				            "price": 0,
				            "count": 0,
				            "status": null
				        },
				        "zfb": {
				            "img": "http://www.luominus.com/222",
				            "price": 3000,
				            "count": 3,
				            "status": 3
				        },
				        "user_name": "收款人3",
				        "user_mobile": "17766666669"
			}
		}
	},
	computed: {
		...mapState(['hasLogin', 'forcedLogin'])
	},
	onLoad() {
			if (!this.hasLogin) {
				uni.showLoading({
				    title: '加载中'
				});
					let validUser = service.getUsers();
					if (validUser.length !== 0) {
						const newData = {
							token:validUser[0].token
						}
						// 存在缓存数据getUsers（）对象
						console.log(validUser[0].token)
						
						setTimeout(function () {
						    uni.hideLoading();
						}, 2000);
						uni.request({  
							url:BASE_URL + "api/v1/Index/getMyCode",  
							data: newData,  
							method:'GET',  
							dataType:'json',  
							header:{  
								'content-type':'application/json'  
							},  
							success: (e) =>{  
								console.log(e) 
								 if (e.statusCode === 200) {
								 	if (e.data.code === 200) {
								 		var myData = e.data.data
								 		this.menus = {
								 			"wx": {
												"img": "http://www.luominus.com/",
												"price": 0,
												"count": 0,
												"status": null
											},
											"zfb": {
												"img": "http://www.luominus.com/222",
												"price": 3000,
												"count": 3,
												"status": 3
											},
											"user_name": "收款人3",
											"user_mobile": "17766666669"
								 		}
								 		console.log(this.menus)
								 	}
								 }
							},  
						})
					} else {
					uni.showModal({
						title: '未登录',
						content: '您未登录，需要登录后才能继续',
						/**
						 * 如果需要强制登录，不显示取消按钮
						 */
						showCancel: !this.forcedLogin,
						success: (res) => {
							if (res.confirm) {
								/**
								 * 如果需要强制登录，使用reLaunch方式
								 */
								if (this.forcedLogin) {
									uni.reLaunch({
										url: '../login/login'
									});
								} else {
									uni.navigateTo({
										url: '../login/login'
									});
								}
							}
						}
					});
				}		
			}
		}
	,
	methods: {
		...mapMutations(['logout']),
		bindLogin() {
			uni.navigateTo({
				url: '../login/login',
			});
		},
		bindLogout() {
			this.logout();
			/**
			 * 如果需要强制登录跳转回登录页面
			 */
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../login/login',
				});
			}
		},
		arrayChange(e){
			// console.log('数组数据返回格式');
			// console.log(e);
		},
		objectChange(e){
			// console.log('对象数据返回格式');
			// console.log(e);
		},
		swiperChange(e){
			this.swiperIndex = e.target.current;
		},
		img(img) {
			console.log(this.menus)
			if (img === 0) {
				return '../../static/img/payZFB.jpg';
			}
			else
			{
				return '../../static/img/payWX.jpeg';
			}
		},
		switchChange (e) {
			let value = e.target.value
			let that = this
			this.$set(this.treatmentStatu, 'checked', value)   // 将点击改变的状态赋给treatmentStatu.checked
			console.log(value)
			if (value && !this.havePromise) {
				if (value === true) {
					const data = {status:2}
					uniRequest.post(BASE_URL + "api/v1/Index/changeWorkStatus", data)
						.then(function(response) {
							console.log(response);
							if (response.status === 200) {
								// console.log(response);
								// 登录成功后存入缓存数据addUser
								service.addUser(data)
								uni.showToast({
									icon: 'none',
									title: '登陆成功',
								});
								// that.toMain(data.account);
								
							}
							else {
								uni.showToast({
									icon: 'none',
									title: '用户账号或密码不正确',
								});
							}
						}).catch(function(error) {
							console.log(error);
						});
				}
				// uni.showModal({
				// 	title: '提示',
				// 	content: '您还没设置接诊承诺，是否前往设置',
				// 	success: function (res) {
				// 		if (res.confirm) {
				// 			that.$emit('changePage', 1)
				// 			console.log('用户点击确定')
				// 		} else if (res.cancel) {
				// 			that.$set(that.treatmentStatu, 'checked', false)  // 手动修改switch的状态，视图会同步更新
				// 			console.log('用户点击取消');
				// 		}
				// 	}
				// });
			}
		}
	}
}
